
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import PopupRadio from 'vux/src/components/popup-radio/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import { OfficialsealList, startApplication } from '@/api/index'
import { mapGetters } from 'vuex'
export default {
name: 'reportLeave',
components: {
PopupPicker, Group, XInput, PopupRadio, Datetime, XTextarea
},
data () {
return {
Officialseal: [],
OfficialType: [{ key: 1, value: '正本原件' }, {key: 2, value: '副本原件'}, { key: 3, value: '正本复印件' }, {key: 4, value: '副本复印件'}, {key: 5, value: '正本扫描件'}, {key: 6, value: '副本扫描件'}],
form: {
type: 21,
companyId: '',
loanItemId: '',
loanItemType: '',
reason: ''
}
}
},
methods: {
showseal () {
this.Officialseal = []
this.getOfficialsealList()
},
getOfficialsealList () {
var allsealList = []
const data = {
companyId: this.form.companyId,
type: 2
}
OfficialsealList(data).then(res => {
allsealList = res.result
allsealList.forEach(e => {
this.Officialseal.push({key: e.id, value: e.name})
})
})
},
startLeave () {
startApplication(this.form)
.then(res => {
this.$vux.toast.show({
text: res.message,
type: 'success'
})
this.$router.go(-1)
})
}
},
computed: {
...mapGetters(['applyLeaveList'])
}
}



